import React from 'react'
import moment from 'moment';
import { Link } from 'react-router-dom';
import { useSelector} from "react-redux";
import Card from '@material-ui/core/Card';
import Button from '@material-ui/core/Button';
import CardMedia from '@material-ui/core/CardMedia';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import EditRoundedIcon from '@material-ui/icons/EditRounded';
import CardActionArea from '@material-ui/core/CardActionArea';
import ThumbUpAltOutlinedIcon from '@material-ui/icons/ThumbUpAltOutlined';


const useStyles = makeStyles({
  root: {
    maxWidth: 345,
  },
  loginbtn:{
    textDecoration: 'none',
    color:'white',
     padding:'1px',
    fontFamily:"Nunito",
  },
  media: {
    height: 140,
  },
});

export default function Cards({card ,upDdate}) {

  const BASE_URL = (process.env.NODE_ENV !== 'development')
 ? 'https://mvacations.herokuapp.com/'
 : '//localhost:1777'
  const user = useSelector((state) => state.user);
  const classes = useStyles();

  const getNewfollow=async()=>{
    let res = await fetch  (`http:${BASE_URL}/users/followers_vacations`,{
      method: "PUT",
      headers: {"Content-Type":"application/json",
      "Authorization": localStorage.token},
      body:JSON.stringify({userId:user._id})
    })

    let data1 = await res.json()
    let res2 = await fetch  (`http:${BASE_URL}/users/unfollowers_vacations`,{
      method: "PUT",
      headers: {"Content-Type":"application/json",
      "Authorization": localStorage.token},
      body:JSON.stringify({userId:user._id})
    })

    let data = await res2.json()
    let newUpVacation = (data1.concat(data))
    upDdate(newUpVacation)
  }
  
  const handleLike = async e => {
    e.preventDefault()
    try {
      let res = await fetch  (`http:${BASE_URL}/flights/follow`,{
        method: "PUT",
        headers: {"Content-Type":"application/json",
        "Authorization": localStorage.token},
        body:JSON.stringify({userId:user._id,flightId:card._id})
      })
       await res.json()
      }catch (error) {
      }
      getNewfollow()
      }
      
  const handleUnLike = async () => {
    try {
      let res = await fetch  (`http:${BASE_URL}/flights/unfollow`,{
        method: "DELETE",
        headers: {"Content-Type":"application/json",
        "Authorization": localStorage.token},
        body:JSON.stringify({userId:user._id,flightId:card._id})
      })
       await res.json()
    } catch (error) {
    }
      getNewfollow()
  }

  return (
    <Card className={classes.root}>
      <CardActionArea>
        <CardMedia
          className={classes.media}
          image={card.photo}
          title={card.location}/>

        <CardContent>
          <Typography gutterBottom variant="h6" component="h2">
          {card.location}
          </Typography>

          <Typography variant="body2" color="textSecondary" component="p">
          Price: {card.price}$
          </Typography>
          
          <Typography >
            check In : {moment(card.checkIn).format("DD/MM/YYYY")}
              <br />
            check Out : {moment(card.checkIn).format("DD/MM/YYYY")}
          </Typography>

          <Typography variant="body2" color="textSecondary" component="p">
          {card.description}
          </Typography>
          
        </CardContent>
      </CardActionArea>
      
      {user.login&&user.role=="user"?
      <>
      {card.followers.filter(i=>i==user._id)==user._id?
      <>
      {Object.keys(card.followers).length}
      <IconButton color="primary" onClick={handleUnLike}>
        <ThumbUpAltOutlinedIcon color="inherit"/>
      </IconButton>
      </>
      :<>
      {Object.keys(card.followers).length}
      <IconButton color="inherit" onClick={handleLike}>
        <ThumbUpAltOutlinedIcon color="inherit"/>
      </IconButton>
      </>}
      </>
      :""}
       
      {user.login && user.role=="admin" ?(
        <>
         <Link to={"/Edit/"+ card._id}className={classes.loginbtn}>
            <Button color="primary">
              <EditRoundedIcon />
              Edit
            </Button>
          </Link>
        </>
      ):(
        <></>
      )}
      <CardActions>
      </CardActions>
    </Card>
  );
}
