let initialUser = {
    login: false,
  };
  
  
  //{type:"LOGIN" , payload:{id,fname,role}}
  //{type:"LOGIN"}
  export const user = (state = initialUser, action) => {
    switch (action.type) {
      case "LOGIN":
        let newState = {
          login: true,
          userNameid: action.payload.userName,
          firstName: action.payload.firstName,
          role: action.payload.role,
          _id:action.payload._id,
        };
        return newState; 
      case "LOGOUT":
        return initialUser;
  
      default:
        return state;
    }
  };
  export const vacation = (state = [], action) => {
    switch (action.type) {
      case "ADD_VACATION":
        return [
          ...state,
          {
            id: 11,
            description: "eE",
            location: "EE",
            photo: "URL",
            price: 344,
            checkIn: "12/22/33",
            checkOut: "33/34/4",
          },
        ];
      case "LOGOUT":
        return initialUser;
  
      default:
        return state;
    }
  };
  
