import React, { useState,useEffect } from 'react'
import {Grid} from '@material-ui/core';
import { Link } from 'react-router-dom';
import Cards from '../components/Cards';
import { Input } from '@material-ui/core';
import Button from '@material-ui/core/Button';
import AppBar from '@material-ui/core/AppBar';
import SearchIcon from '@material-ui/icons/Search';
import DeleteIcon from '@material-ui/icons/Delete';
import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import { useSelector, useDispatch} from "react-redux";
import TrendingUpIcon from '@material-ui/icons/TrendingUp';
import FlightLandIcon from '@material-ui/icons/FlightLand';
import FlightTakeoffIcon from '@material-ui/icons/FlightTakeoff';
import AddCircleOutlineIcon from '@material-ui/icons/AddCircleOutline';

const useStyles = makeStyles ((theme)=>({
    root:{
    marginTop:'20%',
    [theme.breakpoints.down('sm')]: {
      marginTop:'20%',
    },
    [theme.breakpoints.up('md')]: {
        marginTop:'20%',
    },
    [theme.breakpoints.up('lg')]: {
        marginTop:'20%',
    },
    },
    coolText:{
        fontFamily:"Nunito"
    },
    colorText:{
        color:'#0000FF'
    },
    loginbtn:{
        textDecoration: 'none',
        color:'white',
         padding:'1px',
        fontFamily:"Nunito",
      },
     titleFont:{
        fontFamily:"Nunito"
     },
     search:{
        marginTop:'10%'
     }
}))
    export default function Flights() {
    const classes = useStyles();
    const dispatch = useDispatch()
    const [state, setState] = useState([]);    
    const [flight, setFlight] = useState([]);
    const [checkIn, setCheckIn] = useState([])
    const [checkOut, setCheckOut] = useState([])
    const user = useSelector((state) => state.user);
    const [description, setDescripition] = useState("")
    const BASE_URL = (process.env.NODE_ENV !== 'development')
    ? 'https://mvacations.herokuapp.com/'
    : '//localhost:1777'

    useEffect(()=>{
        if (!user.login || user.role === "admin") {
            getAll()
        }else{
            setState(user)
            getAllToUser()
    }},[state])

    const handleSearchWord = async e => {
        e.preventDefault()
        try {
        let res = await fetch  (`http:${BASE_URL}/flights/search`,{
            method: "POST",
            headers: {"Content-Type":"application/json"},
            body:JSON.stringify({description})
        })
        
        let data = await res.json()
        // console.log(data)
      
            setFlight(data)
        
        } catch (error) {
        }}
    const handleSearchDate = async e => {
        e.preventDefault()
        try {
        let res = await fetch  (`http:${BASE_URL}/flights/searchdate`,{
            method: "POST",
            headers: {"Content-Type":"application/json"},
            body:JSON.stringify({ checkIn,checkOut})
        })
        
        let data = await res.json()
        //console.log(data)
      
            setFlight(data)
        
        } catch (error) {
        }}

    const handaleClearWord =  () => {
        if(!user.login||user.role=="admin"){       
            getAll()
            setDescripition("")
           
        }else{
            getAllToUser()
            setDescripition("")
           
        }
    }
    const handaleClearDate =  () => {
        if(!user.login||user.role=="admin"){       
            getAll()
            setCheckOut([])
            setCheckIn([])
        }else{
            getAllToUser()
            setCheckOut([])
            setCheckIn([])
        }
    }
   
    const getAllToUser= async()=>{
        let res = await fetch  (`http:${BASE_URL}/users/followers_vacations`,{
            method: "PUT",
            headers: {"Content-Type":"application/json",
            "Authorization": localStorage.token},
            body:JSON.stringify({userId:state._id})
        }
    )
        let followVacation = await res.json()
        let res2 = await fetch  (`http:${BASE_URL}/users/unfollowers_vacations`,{
            method: "PUT",
            headers: {"Content-Type":"application/json",
            "Authorization": localStorage.token},
            body:JSON.stringify({userId:state._id})
        }
        )
            let unFollowVacation = await res2.json()
            setFlight (followVacation.concat(unFollowVacation))
        }

        const getAll =async()=>{
            let res = await fetch(`http:${BASE_URL}/flights/get_all/`)
            let data = await res.json()
            setFlight(data)
        }

        const Logout = () => {
            dispatch({ type: "LOGOUT" })
            setState(false)
            
        }
    return (
        <>
            <AppBar>
                <Grid container>
                    <Grid item xs={1}>
                        <Typography 
                        gutterBottom
                        color="inherit"
                        className={classes.titleFont}
                        >
                        Welcome {user.login?<>{user.firstName}</>:""} 
                        </Typography>
                    </Grid>
                    <Grid item xs={8}>
                    </Grid>
                    <Grid item xs={1}>
                        <Button color="inherit">
                            {user.login?
                            <div  onClick={Logout}> 
                            <FlightLandIcon className={classes.loginbtn} ></FlightLandIcon>
                            <span className={classes.loginbtn} onClick={Logout}>&nbsp; Logout</span>
                            </div>
                            :
                            <Link to='/login' className={classes.loginbtn}>
                                <FlightTakeoffIcon></FlightTakeoffIcon>
                                &nbsp;  Login
                            </Link>
                            }
                        </Button>
                    </Grid>
                </Grid>
            </AppBar>
            <br />
            <br />
            <div id="search" className={classes.search}>
                <form action="">
                    <Input  placeholder="City" color="primary"  type="text" value={description} onChange={e=>setDescripition(e.target.value)} />
                    &nbsp;&nbsp;
                    <span id="clear">
                        <Button onClick={handleSearchWord}
                         variant="contained"
                          color="primary"
                          disabled={!description.length>0&&!checkIn.length>0&&!checkOut.length>0}><SearchIcon></SearchIcon></Button>
                        <Button onClick={handaleClearWord} variant="contained" color="secondary"><DeleteIcon ></DeleteIcon></Button>
                    </span>
                    <p>{checkIn}</p>
                </form>
                <form action="">
                    <Input title="start date"  placeholder="start_date" color="primary"  value={checkIn} type="date" onChange={e=>setCheckIn(e.target.value)} />
                    <Input title ="end date"  placeholder="end_date" color="primary"  type="date" value={checkOut} onChange={e=>setCheckOut(e.target.value)}/>
                    &nbsp;&nbsp;
                    <span id="clear">
                        <Button onClick={handleSearchDate}
                         variant="contained"
                          color="primary"
                          disabled={!description.length>0&&!checkIn.length>0&&!checkOut.length>0}><SearchIcon></SearchIcon></Button>
                        <Button onClick={handaleClearDate} variant="contained" color="secondary"><DeleteIcon ></DeleteIcon></Button>
                    </span>
                    <p>{checkIn}</p>
                </form>
            {user.login==true&&user.role==="admin"?<>
            <br />
            <Link to="/add" className={classes.loginbtn}>
                <Button color="primary">
                    <AddCircleOutlineIcon />
                    Add Vacation
                </Button>
            </Link>
            <br />
            <Link to="/chart" className={classes.loginbtn}>
                <Button color="primary">
                    <TrendingUpIcon />
                    Chart
                </Button>
            </Link>
            </>:""}
            </div>

            {user.login &&user.role=="user"?
            <Grid container justify="center" container spacing={6} className={classes.root}>
                {flight.map(f=>(
                <Grid item xs={12} sm={4} key={f._id} >
                    <Cards key={f._id}
                    card={f} 
                    upDdate={setFlight}
                    />
                </Grid>
                      ))}
            </Grid>
            :
            <Grid container justify="center" container spacing={6} className={classes.root}>
             {flight.map(f=>(
            <Grid item xs={12} sm={4} key={f._id}>
                <Cards key={f._id} card={f} />
            </Grid>
           ))}
           </Grid>
            }
        </>
    )
}