import React,{useState} from 'react'
import {Grid} from '@material-ui/core'
import { Link } from "react-router-dom";
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import FormControl from '@material-ui/core/FormControl';
import { makeStyles } from '@material-ui/core/styles';
import LockRoundedIcon from '@material-ui/icons/LockRounded';
import InputAdornment from '@material-ui/core/InputAdornment';
import AccountCircleIcon from '@material-ui/icons/AccountCircle';
import MailOutlineRoundedIcon from '@material-ui/icons/MailOutlineRounded';

const useStyles = makeStyles ({
    mainGrid:{
        padding:'10px',
    },
    divall:{
        display:'flex',
        flexDirection:'column',
        maxWidth:'400',
        minWidth:'300'
    },
    linkStyle:{
        color:'black',
        textDecoration: 'none'
    },
    error:{
        color:'red'
    }
})

export default function Register({history}) {
    const classes = useStyles();
    const [firstName, setfirstName] = useState("");
    const [lastName, setlastName] = useState("");
    const [userName, setuserName] = useState("");
    const [password, setpassword] = useState("");
    const [error, setError]= useState('')
    const BASE_URL = (process.env.NODE_ENV !== 'development')
    ? 'https://mvacations.herokuapp.com/'
    : '//localhost:1777'

    const checkUserName = async e=> {
        e.preventDefault()
        try {
            let res = await fetch(`http:${BASE_URL}/users/get_all_user`,{
                method: "put",
                headers: {"Content-Type":"application/json"},
                body:JSON.stringify({userName})
            })
            let data = await res.json()
            if(data.msg===true){
                handleSubmit()
            }else{
                setError("user already exists")
            }
        } catch (error) {
            
        }
    }
   
    const handleSubmit = async ()=>{    
    try {
        let res = await fetch(`http:${BASE_URL}/users/register`,{
        method: "POST",
        headers: {"Content-Type":"application/json"},
        body:JSON.stringify({firstName,lastName,userName,password})
    }
        )
        let data = await res.json()
        if(data.error){
            setError(data.msg)
        }else{
            history.push('/login')
        }
    } catch (err) {   
    }
    }
    return (
        <div >
            <Grid
             container item xs={12} sm={12}
              alignItems="center"
              justify="space-between"
              className={classes.mainGrid}
              direction="column">
                <div className={classes.divall}>
                    <Grid container justify="center">
                        <img 
                        src="https://i.pinimg.com/564x/e8/b7/56/e8b756bb33283fa5f378b349b5313edb.jpg"
                        alt="logo of plane"
                        width={200}
                        ></img>
                    </Grid>
                    <FormControl >

                        <TextField
                        onChange={e=>setfirstName(e.target.value)} 
                        label="first Name" 
                        margin="normal" 
                        InputProps={{startAdornment:(
                            <InputAdornment position="start">
                                <AccountCircleIcon />
                            </InputAdornment>),}}
                        />

                        <TextField
                        onChange={e=>setlastName(e.target.value)} 
                        label="Last Name" 
                        margin="normal" 
                        InputProps={{startAdornment:(
                            <InputAdornment position="start">
                                <AccountCircleIcon />
                            </InputAdornment>),}}
                        />

                        <TextField
                        onChange={e=>setuserName(e.target.value)}
                        type="mail"
                        label="User Name" 
                        margin="normal" 
                        InputProps={{startAdornment:(
                            <InputAdornment position="start">
                                <MailOutlineRoundedIcon />
                            </InputAdornment>),}}
                        />

                        <TextField
                        onChange={e=>setpassword(e.target.value)} 
                        label="Password"
                        type="password"  
                        margin="normal" 
                        InputProps={{startAdornment:(
                            <InputAdornment position="start">
                                <LockRoundedIcon />
                            </InputAdornment>),}}
                        />

                        <Button 
                        color='primary' 
                        variant='contained' 
                        type="submit"
                        onClick={checkUserName}
                        >Register
                        </Button>

                    </ FormControl >
                    <Grid container justify="center" spacing={2}>
                        <Grid item>
                            <Link to="/"  className={classes.linkStyle}>Back</Link>
                        </Grid>
                        <Grid item>
                            <Link to="/login" className={classes.linkStyle}>Login</Link>
                        </Grid>
                        
                    </Grid>
                    <Grid item><p className={classes.error}>{error}</p></Grid>
                </div>
            </Grid>
        </div>
)
}