import React, { useEffect, useState } from "react"
import moment from 'moment';
import {Grid} from '@material-ui/core'
import { Link } from "react-router-dom";
import { useSelector } from "react-redux";
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import { makeStyles } from '@material-ui/core/styles';
import FormControl from '@material-ui/core/FormControl';
import LocationOnIcon from '@material-ui/icons/LocationOn';
import LocalOfferIcon from '@material-ui/icons/LocalOffer';
import InputAdornment from '@material-ui/core/InputAdornment';
import LocationCityIcon from '@material-ui/icons/LocationCity';
import AlarmOffRoundedIcon from '@material-ui/icons/AlarmOffRounded';
import AccessAlarmsRoundedIcon from '@material-ui/icons/AccessAlarmsRounded';
import PhotoSizeSelectActualIcon from '@material-ui/icons/PhotoSizeSelectActual';

export default function EditVacation({match, history}) {
  
  const useStyles = makeStyles ({
    mainGrid:{
      padding:'10px',
      
    },
    divall:{
        display:'flex',
        flexDirection:'column',
        maxWidth:'400',
        minWidth:'300'
      },
      linkStyle:{
        color:'black',
        textDecoration: 'none'
      },
      error:{
        color:'red'
    }
    })  
    
    const classes = useStyles();
    //const [_id, setId] = useState("");
    const [photo, setPhoto] = useState("");
    const [price, setPrice] = useState("");
    const [checkIn, setCheckIn] = useState([]);
    const [checkOut, setCheckOut] = useState([]);
    const [location, setLocation] = useState("");
    const [description, setDescription] = useState("");
    const user = useSelector((state) => state.user);
    const BASE_URL = (process.env.NODE_ENV !== 'development')
    ? 'https://mvacations.herokuapp.com/'
    : '//localhost:1777'
    
  useEffect(() => { if (!user.login || user.role === "user") {
    history.push("/");
    }
    (async () => {
      let res = await fetch(
        `http:${BASE_URL}/flights/edit/` + match.params._id
      );
      let data = await res.json();
      
      setDescription(data.description)
      setLocation(data.location)
      setPhoto(data.photo)
      setPrice(data.price)
      setCheckIn(moment(data.checkIn).format('yyyy-MM-DD'))
      setCheckOut(moment(data.checkOut).format('yyyy-MM-DD'))
    })();
  }
  ,[]
  );

  const haedleSubmit = async e => {
    e.preventDefault()
        try {
          let res = await fetch(`http:${BASE_URL}/flights/edit_flights/`+match.params._id, {
            method: "PUT",
            headers: {
              "Content-Type": "application/json",
              "Authorization": localStorage.token,
            },
            body: JSON.stringify({
                description,
                location,
                photo,
                price,
                checkIn,
                checkOut
            }),
          });
          await res.json()
          history.push("/");
        } catch (error) {
        }
      };

  return (
    <div>
      <Grid
        container item xs={12} sm={12}
        alignItems="center"
        className={classes.mainGrid}
        direction="column">
        <div className={classes.divall}>
            <Grid container justify="center">
                <img 
                src="https://i.pinimg.com/564x/e8/b7/56/e8b756bb33283fa5f378b349b5313edb.jpg"
                alt="logo of plane"
                width={200}
                ></img>
            </Grid>

            <FormControl >
              <TextField
              value={description}
              onChange={e=>setDescription(e.target.value)} 
              label="Description" 
              margin="normal" 
              InputProps={{startAdornment:(
                <InputAdornment position="start">
                    <LocationCityIcon />
                </InputAdornment>),}}
              />
              <TextField
              value={location}
              onChange={e=>setLocation(e.target.value)} 
              label="Location" 
              margin="normal" 
              InputProps={{startAdornment:(
                <InputAdornment position="start">
                    <LocationOnIcon />
                </InputAdornment>),}}
              />

              <TextField
              value={photo}
              onChange={e=>setPhoto(e.target.value)}
              label="Photo" 
              margin="normal" 
              InputProps={{startAdornment:(
                <InputAdornment position="start">
                    <PhotoSizeSelectActualIcon />
                </InputAdornment>),}}
              />

              <TextField
              value={price}
              onChange={e=>setPrice(e.target.value)} 
              label="price"
              type="number"  
              margin="normal" 
              InputProps={{startAdornment:(
                <InputAdornment position="start">
                    <LocalOfferIcon />
                </InputAdornment>),}}
              />
              
              <TextField
              value={checkIn}
              onChange={e=>setCheckIn(e.target.value)} 
              label="Check In"
              type="date"  
              margin="normal" 
              InputProps={{startAdornment:(
                <InputAdornment position="start">
                  <AccessAlarmsRoundedIcon />
                </InputAdornment>),}}
              />

              <TextField
              value={checkOut}
              onChange={e=>setCheckOut(e.target.value)} 
              label="CheckOut"
              type="date"  
              margin="normal" 
              InputProps={{startAdornment:(
                <InputAdornment position="start">
                  <AlarmOffRoundedIcon />
                </InputAdornment>),}}
              />

              <Button 
              color='primary' 
              variant='contained' 
              type="submit"
              onClick={haedleSubmit}>
              Save Changes
              </Button>
            </FormControl>
             <Grid container justify="center" spacing={2}>
                <Grid item>
                    <Link to="/"  className={classes.linkStyle}>Back</Link>
                </Grid>
            </Grid> 
        </div>
      </Grid>
    </div>
  );
}
