import React from 'react'
import Flights from './Flights'
import {Grid} from '@material-ui/core'

export default function Main() {

    return (
        <Grid container direction="column">
            <Grid item >
            </Grid>
            <Grid item container>
                <Grid item xs={false} sm={2} />
                <Grid item xs={12} sm={8} >
                    <Flights /> 
                </Grid>
                <Grid item xs={false} sm={2} />
            </Grid>
        </Grid>
    )
}

