import React, {useState} from 'react'
import decode from 'jwt-decode'
import {Grid} from '@material-ui/core'
import { Link } from "react-router-dom";
import { useDispatch } from "react-redux";
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import { makeStyles } from '@material-ui/core/styles';
import FormControl from '@material-ui/core/FormControl';
import LockRoundedIcon from '@material-ui/icons/LockRounded';
import InputAdornment from '@material-ui/core/InputAdornment';
import AccountCircleIcon from '@material-ui/icons/AccountCircle';

const useStyles = makeStyles ({
    mainGrid:{
        padding:'10px',
       
    },
    divall:{
        display:'flex',
        flexDirection:'column',
        maxWidth:'400',
        minWidth:'300'
    },
    linkStyle:{
        color:'black',
        textDecoration: 'none'
    },
    error:{
        color:'red',
    },
    admin:{
        color:'blue',
    }
})

export default function Login( {history} ) {
    const classes = useStyles();
    const [userName, setuserName]= useState('')
    const [password, setpassword]= useState('')
    const [error, setError]= useState('')
    const dispatch = useDispatch();
    const BASE_URL = (process.env.NODE_ENV !== 'development')
    ? 'https://mvacations.herokuapp.com/'
    : '//localhost:1777'
    
    const handleSubmit = async e=>{
        e.preventDefault()
    try {
        let res = await fetch(`http:${BASE_URL}/users/login`,{
        method: "POST",
        headers: {"Content-Type":"application/json"},
        body:JSON.stringify({userName,password})
    }
        )
        let data = await res.json()
        if(data.error===true){
            setError(data.msg)
        }else{
            let _id = data.result[0]._id
            localStorage.token=data.token
            let { userName,firstName, role } = decode(data.token);
            dispatch({ type: "LOGIN", payload: { userName,firstName, role,_id } });
        history.push('/')
        }
    } catch (err) {    
    }
    }

    return (
        <div >
            <Grid
              container item xs={12} sm={12}
              alignItems="center"
              justify="space-between"
              className={classes.mainGrid}
              direction="column">
                <div className={classes.divall}>
                    <Grid container justify="center">
                        <img 
                        src="https://i.pinimg.com/564x/e8/b7/56/e8b756bb33283fa5f378b349b5313edb.jpg"
                        alt="logo of plane"
                        width={200}
                        ></img>
                    </Grid>
                    <FormControl >

                        <TextField
                        onChange={e=>setuserName(e.target.value)} 
                        label="User Name" 
                        margin="normal" 
                        InputProps={{startAdornment:(
                            <InputAdornment position="start">
                                <AccountCircleIcon />
                            </InputAdornment>),}}
                        />

                        <TextField
                        onChange={e=>setpassword(e.target.value)} 
                        label="Password"
                        type="password"  
                        margin="normal" 
                        InputProps={{startAdornment:(
                            <InputAdornment position="start">
                                <LockRoundedIcon />
                            </InputAdornment>),}}
                        />

                        <Button 
                        color='primary' 
                        variant='contained' 
                        type="submit"
                        onClick={handleSubmit}
                        >
                        Login
                        </Button>
                    </ FormControl >
                    <Grid container justify="center" spacing={2}>
                        <Grid item>
                            <Link to="/"  className={classes.linkStyle}>Back</Link>
                        </Grid>
                        <Grid item>
                            <Link to="/register" className={classes.linkStyle}>Register</Link>
                        </Grid>
                    </Grid>
                    <Grid item><p className={classes.error}>{error}</p></Grid>
                    <Grid item>
                        <p className={classes.admin}>Login as admin:</p>
                        <p> Email: admin@admin ,Password: admin$765</p>
                    </Grid>
                </div>
            </Grid>
        </div>
    )
}